/* site-list */

.site-list {
  text-align: center;
  font-weight: 700;
}

.site-list__item {
  width: 100%;
  padding-top: 16px;
  padding-bottom: 15px;
  margin-bottom: 1px;
  box-shadow: 0 1px 0 0 rgba(0, 0, 0, 1), inset 0 1px 0 0 rgba(255, 255, 255, 0.15);

  &:first-of-type {
    box-shadow: 0 1px 0 0 rgba(0, 0, 0, 1), inset 0 1px 0 0 rgba(255, 255, 255, 0.15), 0 -1px 0 0 rgba(0, 0, 0, 1);
  }
}

.site-list__link {
  position: relative;

  font-size: 18px;
  font-style: normal;
  line-height: 32px;
  color: $white;
  text-transform: uppercase;
  text-decoration: none;

  &--active::after {
    content: "";
    position: absolute;
    bottom: -5px;
    left: 0;

    width: 100%;
    height: 2px;

    background-color: $white;
  }

  &:hover[href],
  &:focus[href] {
    color: $pink;
  }

  &:active[href] {
    color: $white;
    opacity: 0.3;
  }
}

@media (min-width: $desktop-width) {
  .site-list__item {
    width: auto;

    box-shadow: none;

    &:first-of-type {
      box-shadow: none;
    }
  }

  .site-list__link {
    font-weight: 300;
    line-height: 18px;
    text-transform: none;

    &--active::after {
      bottom: -10px;
    }
  }
}
